import * as React from 'react';
import {useState} from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Language from '@mui/icons-material/Language';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {createTheme, ThemeProvider} from '@mui/material/styles';
import {Copyright} from "./Copyright";
import vocab from './vocabulary/vocabulary.json'
import {Settings} from "@mui/icons-material";
import {Card, CardContent, Paper, Toolbar} from "@mui/material";
import IconButton from "@mui/material/IconButton";
import Drawer from "@mui/material/Drawer";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";

const theme = createTheme();

//Tiempos
const PERFECTO = 'Perfecto'
const INDEFINIDO = 'Indefinido'
const IMPERFECTO = 'Imperfecto'
const FUTURO_IMP = 'Futuro Imperfecto'

//Groups
const Group = {
  "PERFECTO": {"key": 'PERFECTO', "name": 'Perfecto', "tiempo": PERFECTO},
  "INDEFINIDO_AR": {"key": 'INDEFINIDO_AR', "name": 'Indefinido -ar', "tiempo": INDEFINIDO},
  "INDEFINIDO_ER_IR": {"key": 'INDEFINIDO_ER_IR', "name": 'Indefinido -er/-ir', "tiempo": INDEFINIDO},
  "IMPERFECTO_AR": {"key": 'IMPERFECTO_AR', "name": 'Imperfecto -ar', "tiempo": IMPERFECTO},
  "IMPERFECTO_ER_IR": {"key": 'IMPERFECTO_ER_IR', "name": 'Imperfecto -er/-ir', "tiempo": IMPERFECTO},
  "FUTURO_IMP": {"key": 'FUTURO_IMP', "name": 'Futuro Imperfecto', "tiempo": FUTURO_IMP},
  "FUTURO_IMP_IRR": {"key": 'FUTURO_IMP_IRR', "name": 'Futuro Imperfecto Irregular', "tiempo": FUTURO_IMP},
}

const allGroups = [
  Group.PERFECTO, Group.INDEFINIDO_AR, Group.INDEFINIDO_ER_IR, Group.IMPERFECTO_AR,
  Group.IMPERFECTO_ER_IR, Group.FUTURO_IMP, Group.FUTURO_IMP_IRR
]

const vocabByGroup = {
  [Group.PERFECTO.key]: findVocabPerfecto(),
  [Group.INDEFINIDO_AR.key]: findVocabIndefinido('ar'),
  [Group.INDEFINIDO_ER_IR.key]: findVocabIndefinido('er').concat(findVocabIndefinido('ir')),
  [Group.IMPERFECTO_AR.key]: findVocabImperfecto('ar'),
  [Group.IMPERFECTO_ER_IR.key]: findVocabImperfecto('er').concat(findVocabImperfecto('ir')),
  [Group.FUTURO_IMP.key]: findVocabFuturoImp(null),
  [Group.FUTURO_IMP_IRR.key]: findVocabFuturoImp('irr')
}

function findVocabPerfecto() {
  return Object.entries(vocab).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && desc.perfecto?.group == null
  }).map(el => el[0]);
}

function findVocabIndefinido(end) {
  return Object.entries(vocab).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.indefinido?.group == null
  }).map(el => el[0]);
}

function findVocabImperfecto(end) {
  return Object.entries(vocab).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.imperfecto?.group == null
  }).map(el => el[0]);
}

function findVocabFuturoImp(group) {
  return Object.entries(vocab).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && (desc.futuroImp?.group || null) === group
  }).map(el => el[0]);
}


const personas = ['Yo', 'Tu', 'El', 'Nosotros', 'Vosotros', 'Ellos']

function findHint(tiempo, verbo, persona) {
  if (tiempo === INDEFINIDO) {
    return indefinido(verbo, persona)
  } else if (tiempo === IMPERFECTO) {
    return imperfecto(verbo, persona)
  } else if (tiempo === PERFECTO) {
    return perfecto(verbo, persona)
  } else if (tiempo === FUTURO_IMP) {
    return futuroImp(verbo, persona)
  }
}

function indefinido(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['Ã©', 'aste', 'Ã³', 'amos', 'asteis', 'aron']
    return base + ends[persona]
  } else {
    const ends = ['Ã­', 'iste', 'iÃ³', 'imos', 'isteis', 'ieron']
    return base + ends[persona]
  }
}

function futuroImp(verbo, persona) {
  const root = vocab[verbo].futuroImp?.root || verbo
  const ends = ['Ã©', 'Ã¡s', 'Ã¡', 'emos', 'Ã©is', 'Ã¡n']
  return root + ends[persona]
}

function imperfecto(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['aba', 'abas', 'aba', 'Ã¡bamos', 'abais', 'aban']
    return base + ends[persona]
  } else {
    const ends = ['Ã­a', 'Ã­as', 'Ã­a', 'Ã­amos', 'Ã­ais', 'Ã­an']
    return base + ends[persona]
  }

}

function perfecto(verbo, persona) {
  const haber = ['he', 'has', 'ha', 'hemos', 'habÃ©is', 'han']
  return haber[persona] + ' ' + participio(verbo)

}

function participio(verbo) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    return base + 'ado'
  } else {
    return base + 'ido'
  }
}

function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

function shuffleArray(array) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

function randomCombinations(groups) {
  const combinations = []
  groups.forEach(group => {
    for (let index in personas) {
      combinations.push([group, index])
    }
  })
  shuffleArray(combinations)
  return combinations
}

function capitalize(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export default function SignIn() {

  const [enabledGroups, setEnabledGroups] = useState([Group.FUTURO_IMP])
  const [state, setState] = useState(() => nextState({"combinations": []}))
  const [hint, setHint] = useState('')
  const [isSettingsOpen, setSettingsOpen] = useState(false)

  function switchEnabledGroup(enabledGroups, group) {
    if (enabledGroups.includes(group)) {
      if(enabledGroups.length > 1) {
        return enabledGroups.filter(grp => grp !== group)
      } else {
        return enabledGroups
      }
    } else {
      return enabledGroups.concat(group)
    }
  }

  function nextState(currState) {
    let combinations = [...currState.combinations]
    if (combinations.length === 0 || currState.groups !== enabledGroups) {
      combinations = randomCombinations(enabledGroups)
    }
    const [group, persona] = combinations.pop()

    const vocab = vocabByGroup[group.key]
    return {
      "groups": enabledGroups,
      "combinations": combinations,
      "tiempo": group.tiempo,
      "verbo": vocab[getRandomInt(vocab.length)],
      "persona": persona,
    }
  }

  function findHintFor(state) {
    return findHint(state.tiempo, state.verbo, state.persona)
  }

  function onClickNext() {
    setHint('')
    setState(nextState)
  }

  function onClickHint() {
    setHint(findHintFor(state))
  }

  function getProgress() {
    const total = enabledGroups.length * personas.length
    return total - state.combinations.length + '/' + total
  }

  function getExercise() {
    if (hint) {
      return hint
    } else {
      return state.verbo
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline/>
        <Toolbar disableGutters sx={{borderBottom: 1, borderColor: 'divider'}}>
          <Typography
            variant="h5"
            color="inherit"
            align="left"
            sx={{flex: 1}}
          >
            Conjugador
          </Typography>
          <IconButton>
            <Settings fontSize="large" color="secondary" onClick={() => setSettingsOpen(true)}/>
            <Drawer
              anchor="top"
              open={isSettingsOpen}
              onClose={() => {
                setState(nextState)
                setSettingsOpen(false)
              }}
            >
              <Container component="main" maxWidth="xs">
                <FormControl component="fieldset" variant="standard">
                  <FormLabel component="legend">PretÃ©ritos</FormLabel>
                  <FormGroup>
                    <FormControlLabel
                      control={
                        <Switch checked={enabledGroups.includes(Group.PERFECTO)}
                                onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.PERFECTO))}
                        />
                      }
                      label={Group.PERFECTO.name}
                    />
                    <FormControlLabel
                      control={
                        <Switch checked={enabledGroups.includes(Group.INDEFINIDO_AR)}
                                onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.INDEFINIDO_AR))}
                        />
                      }
                      label={Group.INDEFINIDO_AR.name}
                    />
                    <FormControlLabel
                      control={
                        <Switch checked={enabledGroups.includes(Group.INDEFINIDO_ER_IR)}
                                onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.INDEFINIDO_ER_IR))}
                        />
                      }
                      label={Group.INDEFINIDO_ER_IR.name}
                    />
                    <FormControlLabel
                      control={
                        <Switch checked={enabledGroups.includes(Group.IMPERFECTO_AR)}
                                onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.IMPERFECTO_AR))}
                        />
                      }
                      label={Group.IMPERFECTO_AR.name}
                    />
                    <FormControlLabel
                      control={
                        <Switch checked={enabledGroups.includes(Group.IMPERFECTO_ER_IR)}
                                onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.IMPERFECTO_ER_IR))}
                        />
                      }
                      label={Group.IMPERFECTO_ER_IR.name}
                    />
                  </FormGroup>
                  <FormLabel component="legend">Futuro</FormLabel>
                  <FormControlLabel
                    control={
                      <Switch checked={enabledGroups.includes(Group.FUTURO_IMP)}
                              onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.FUTURO_IMP))}
                      />
                    }
                    label={Group.FUTURO_IMP.name}
                  />
                  <FormControlLabel
                    control={
                      <Switch checked={enabledGroups.includes(Group.FUTURO_IMP_IRR)}
                              onChange={() => setEnabledGroups(currGroups => switchEnabledGroup(currGroups, Group.FUTURO_IMP_IRR))}
                      />
                    }
                    label={Group.FUTURO_IMP_IRR.name}
                  />
                </FormControl>
              </Container>
            </Drawer>
          </IconButton>
        </Toolbar>
        <Card sx={{mt: 2}}>
          <CardContent>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <Typography id="progress" variant="h6" align="left"
                            color="text.secondary">{state.tiempo}</Typography>
              </Grid>
              <Grid item xs={6}>
                <Typography id="progress" variant="h6" align="right"
                            color="text.secondary">{getProgress()}</Typography>
              </Grid>
              <Grid item xs={12} sx={{mt: 5, mb: 8}}>
                <Typography variant="h5" id="exercise" align="center">
                  {personas[state.persona] + ' '}
                  <Box sx={{display: "inline"}} color={hint ? "success.main" : "warning.main"}>{getExercise()}</Box>
                </Typography>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
        <Grid container spacing={2} sx={{mt: 1}}>
          <Grid item xs={5}>
            <Button type="submit" fullWidth variant="contained" onClick={onClickHint}>Hint</Button>
          </Grid>
          <Grid item xs={7}>
            <Button type="submit" fullWidth variant="contained" onClick={onClickNext}>Next</Button>
          </Grid>
        </Grid>
        <Copyright sx={{mt: 4, mb: 4}}/>
      </Container>
    </ThemeProvider>
  );
}